import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import './Header.scss';
import LogoutIcon from '../../images/logout.svg';
import * as pageUrls from '../constants/pageUrls';
 class Header extends Component {
    constructor(props) {
        super(props);
      }

    render() {
        let {showLogout=true, history} = this.props;
        return (
            <div className={'header-container'}>
                <div className={'box'}>
                 BBM Disciple Tracker
        <div onClick={()=>this.props.history.push(pageUrls.HOME)}
        className={'logout'}>
            {!showLogout?<img src={LogoutIcon}/>:null}</div>
                 </div>
                <div className={'curve'}/>
            </div>
        )
    }
}

export default Header